import type { ValueToTextRule } from '@ali/4ever-cangjie';
import Sticker from '../models';

const valueToText: ValueToTextRule = {
  name: 'sticker',

  match(node) {
    return Sticker.isSticker(node);
  },

  convert(node) {
    // @ts-ignore
    return `[${node?.data?.code || '表情'}]`;
  },
};

export default valueToText;
